import { Component, OnInit } from '@angular/core';
import { CoolLocalStorage } from "angular2-cool-storage";
import { Router, ActivatedRoute, Params} from "@angular/router";
import { Session, User } from "./../user/user"
import { UserService } from "./../user/user.service";
import { QuestionaryService } from "./questionary.service";
import { Question, Questionary, Option, Step } from "./types";

@Component({
  moduleId: module.id,
  selector: 'questionary-edit',
  templateUrl: './questionary-edit.component.html'
})
export class QuestionaryEditComponent implements OnInit {
  session: Session;
  userService: UserService;
  qService: QuestionaryService;
  questionary: Questionary; 
  currentStep: Step;
  currentQuestion : Question;

  constructor (private route: ActivatedRoute, private router: Router, userService: UserService, questionService:QuestionaryService) {
    this.userService = userService;
    this.session = userService.session;
    this.qService = questionService;
    this.questionary = null;
    this.currentStep = null;
  }
  ngOnInit():void  {

    this.userService.getUser()
      .then(u => {
        if (u.type == "admin"){
          this.route.params.forEach((params: Params) => {
            if (params['questionaryId'] !== undefined) {
              var auxId = params["questionaryId"];

              var stepId = (params['stepId'] !== undefined)? params['stepId'] : null;
              if (stepId == null) {
                this.qService.getQuestionary(auxId)
                .then(q => {
                  this.questionary = q;
                  console.log(q);
                  if (q.steps.length >0) {
                    this.router.navigate(['/questionaries/'+auxId+'/steps/'+q.steps[0].uid+'/edit']);
                  }
                });
              }
              else {
                this.qService.getQuestionary(auxId)
                .then(q => {
                  this.questionary = q;
                  if (q.steps.length>0){
                    this.setStep(q.steps.find(it => it.uid == stepId));
                  }
                });
              }
            }
          });
        }
        else {
          this.router.navigate(['/dashboard']);
        }
      });
  }

  changeQuestion(q: Question):void {
    this.currentQuestion = q;
  }

  changeStep(d) {
    this.router.navigate(['/questionaries/'+this.questionary.uid+'/steps/'+d.uid+'/edit']);
  }
  setStep(s):void {
    this.currentStep = s;
    console.log(s);
    this.qService.getQuestions(s.uid)
      .then(qs => {
        this.currentStep.questions = qs;
        console.log("Loading questions ");
        console.log(qs);
      });
  }

  editStep(): void {
    this.router.navigate(['/questionaries/'+this.questionary.uid+ '/steps/' + this.currentStep.uid+'/edit']);
  }

  addStep(): void {
    this.router.navigate(['/questionaries/'+this.questionary.uid+ '/steps/add']);
  }
  addQuestion(): void {
    this.router.navigate(['/steps/'+this.currentStep.uid+ '/questions/add']);
  }

  addMatch(): void {
    this.router.navigate(['/questionaries/'+ this.questionary.uid + '/matchs/add']);
  }
}




