import { Component, OnInit } from '@angular/core';
import { CoolLocalStorage } from "angular2-cool-storage";
import { Router } from "@angular/router";
import { Session, User } from "./../user/user"
import { UserService } from "./../user/user.service";
import { QuestionaryService } from "./questionary.service";
import { TakenQuiz, Questionary, Option, Step } from "./types";

@Component({
  moduleId: module.id,
  selector: 'dashboard',
  templateUrl: './dashboard.component.html'
})
export class DashboardComponent implements OnInit {
  session: Session;
  userService: UserService;
  questionaries: Questionary[] = [];
  qService: QuestionaryService;
  takenQuizzes: TakenQuiz[];
  isAdmin: boolean;

  constructor (private router: Router, userService: UserService, questionService:QuestionaryService) {
    this.userService = userService;
    this.session = userService.session;
    this.qService = questionService;
    this.takenQuizzes =null;
  }
  ngOnInit():void  {
    this.qService.getQuestionaries() 
      .then(qs => {this.questionaries = qs; console.log(qs)});
    this.qService.getTakenQuizzes()
      .then(tq => {this.takenQuizzes = tq; });
    this.userService.getUser()
      .then(u => this.isAdmin = (u.type == "admin" ));
  }
  ngOnChange(): void {
  }

  changeStep(step:Step) {
  }
  addQuestionary() {
    this.router.navigate(["/questionaries/add"]);
  }
  editQuestionary(q:Questionary):void {
    if (q != null) {
      this.router.navigate(['/questionaries/'+q.uid+'/edit']);
    }
  }
  takeQuestionary(q:Questionary, tq: TakenQuiz): void {
    if (tq == null) {
      var tq = new TakenQuiz();
      tq.questionary_id = q.uid;
      this.qService.addTakenQuiz(tq)
        .then(tt => {
          tq = tt;
          this.router.navigate(['/take/'+tq.uid]);
        });
    } else {
      this.router.navigate(['/take/'+tq.uid]);
    }
    
  }
  actionQuestionary(q: Questionary):void {
    this.router.navigate(['/questionaries/'+q.uid]);
  }
  logout (): void {
  }
}




