export class Question{
  uid: string;
  code: string;
  text: string;
  type: string;
  user_id: string;
  sort: number;
  options: Option[];
  logics: Logic[];
  step_id: string;
  visible: boolean;
  mandatory: boolean;

  static TYPES = [
    "radio",
    "level", 
    "remark",
    "text"
  ];
}
export class Option{
  uid: string;
  text: string;
  value: number;
  type: string;
  user_id: string;
  question_id: string;
  sort: number;
}

export class SelectionData {
  option_id: string;
  value: number;
  text: string;
  accepted: boolean;
  question_id: string;
}

export class Step {
  uid: string;
  code: string;
  text: string;
  sort: number;
  questionary_id: string;
  questions: Question[];
}
export class Questionary {
  uid: string;
  code: string;
  text: string;
  steps: Step[];
}

export class TakenQuiz {
  uid: string;
  questionary_id: string;
  questionary: Questionary;
}

export class Selection {
  uid: string;
  option: Option;
  question: Question;
  question_id: string;
  taken_quiz: TakenQuiz;
  taken_quiz_id: string;
  option_id: string;
  value: number;
  valueText: string;
}
export class MatchLogic {
  uid: string;
  bool: string;
  parent_id: string;
  target_id: string;
  target_type: string;

  static TARGET_TYPES = [
    "match", "match-logic"
  ];
  static BOOL_TYPES = [
    "and", 
    "or"
  ];
}

export class Match {
  uid: string;
  question_id: string;
  operator: string;
  // Single | Array
  type: string;
  target_question_id: string;
  target_type_id: string;
  // Option Id or ArrayOption according to type
  target_option_id: string
  target_value_id: number;

  static OPERATORS = [
    "eq",
    "gt",
    "lt",
    "bt"
  ];
}

export class JsonLogic {
  bool: string;
  children: JsonLogic[];
}

export class Logic {
  uid: string;
  action: string;
  question_id: string;


  static ACTIONS = [
    "show", 
    "hide"
  ];
}



