import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import { CoolLocalStorage } from "angular2-cool-storage";
import { Router, ActivatedRoute, Params} from "@angular/router";
import { Session, User } from "./../user/user"
import { UserService } from "./../user/user.service";
import { QuestionaryService } from "./questionary.service";
import { Questionary, Match, Question, Option, Step } from "./types";

@Component({
  moduleId: module.id,
  selector: 'match-add',
  templateUrl: './match-add.component.html'
})
export class MatchAddComponent implements OnInit {
  session: Session;
  userService: UserService;
  qService: QuestionaryService;
  match: Match = new Match();
  questionary: Questionary;
  question: Question;
  steps: Step[];
  selStep: Step;
  selOStep : Step;
  questions: Question[];
  selQuestion: Question;
  SelOQuestion: Question;
  selOption: Option;

  constructor (private _location: Location, private route: ActivatedRoute, private router: Router, userService: UserService, questionService:QuestionaryService) {
    this.userService = userService;
    this.session = userService.session;
    this.qService = questionService;
  }
  ngOnInit():void  {
    this.route.params.forEach((params: Params) => {
      this.match.question_id = params['questionId'];
      if (params['questionaryId'] !== undefined) {
        this.qService.getQuestionary(params['questionaryId'])
          .then(qu => {
            this.questionary = qu;
        });
      }
    });
  }

  getOperatorTypes() {
    return Match.OPERATORS;
  }

  stepSelected() {
    if (this.selOStep != null) {
      if (this.selOStep.questions === undefined) {
        this.qService.getQuestions(this.selOStep.uid)
          .then ( qs => {
            console.log(qs);
            this.selOStep.questions = qs;
          });
      }
    }
    if (this.selStep != null) {
      if (this.selStep.questions === undefined) {
        this.qService.getQuestions(this.selStep.uid)
          .then ( qs => {
            console.log(qs);
            this.selStep.questions = qs;
          });
      }
    }
  }
  questionSelected() {
    if (this.selQuestion != null) {
      console.log(this.selQuestion);
    }
  }
  optionSelected() {
  }
  create() {
    console.log(this.match);
    this.qService.addMatch(this.match);
    this._location.back();
  }
  cancel() {
    //this.router.navigate(['/questionaries/'+this.sId]);
    this._location.back();
  }
}




