import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { AppRoutingModule } from "./app-routing.module";

import { CoolStorageModule, CoolLocalStorage } from "angular2-cool-storage";

import { AppComponent } from './app.component';

//Services 
import { UserService } from "./user/user.service";
import { RestService } from "./rest.service";
import { QuestionaryService } from "./questionary/questionary.service";

// Componenets
import { UserLoginComponent }  from './user/user-login.component';
import { UserRegisterComponent }  from './user/user-register.component';
import { HeaderComponent } from './header.component';
import { HomeComponent } from './home.component';

import { DashboardComponent } from './questionary/dashboard.component';
import { QuestionaryAddComponent } from './questionary/questionary-add.component';
import { QuestionaryActionComponent } from './questionary/questionary-action.component';
import { QuestionaryEditComponent } from './questionary/questionary-edit.component';

import { StepAddComponent } from './questionary/step-add.component';
import { StepActionComponent } from './questionary/step-action.component';
import { StepEditComponent } from './questionary/step-edit.component';

import { QuestionAddComponent }  from './questionary/question-add.component';
import { QuestionEditorComponent }  from './questionary/question-editor.component';
import { MatchAddComponent } from './questionary/match-add.component';
import { TakenQuizActionComponent } from './questionary/taken-quiz-action.component';

import { TreeViewComponent } from "./tree-view/tree-view.component"

@NgModule({
  declarations: [
    AppComponent,
    UserLoginComponent,
    UserRegisterComponent,
    HeaderComponent,
    HomeComponent, 
    DashboardComponent,

    QuestionaryAddComponent,
    QuestionaryActionComponent,
    QuestionaryEditComponent,

    StepAddComponent,
    StepEditComponent,
    StepActionComponent,
    
    QuestionAddComponent,
    QuestionEditorComponent,
    MatchAddComponent,

    TakenQuizActionComponent,
    TreeViewComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    CoolStorageModule,
    HttpModule,
    AppRoutingModule
  ],
  providers: [
    RestService,
    UserService,
    QuestionaryService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



