    getQuestionary(uid: String): Promise<Questionary> {
      return this.http.get(this.restService.getServerPath() + '/questionaries/' + uid ,
                           {headers: this.restService.createHeaders()})
          .toPromise()
          .then( res => {
            var aux = res.json();
            return aux.data as Questionary;
          })
          .catch(err => { 
            console.log ("Retrieving questionary failed");
            console.log(err);
            if (err.status == 401) {
            }
            //return Promise.resolve();
            return Promise.resolve();
          });
    }
