import { Input, Component, OnInit, SimpleChange } from '@angular/core';
import { Location } from '@angular/common';
import { CoolLocalStorage } from "angular2-cool-storage";
import { Router, ActivatedRoute, Params} from "@angular/router";
import { Session, User } from "./../user/user"
import { UserService } from "./../user/user.service";
import { QuestionaryService } from "./questionary.service";
import { JsonLogic, Logic, MatchLogic, Match, Question, Questionary, Option, Step } from "./types";
import { TreeNode } from "./../tree-view/types";

@Component({
  moduleId: module.id,
  selector: 'question-editor',
  templateUrl: './question-editor.component.html'
})
export class QuestionEditorComponent implements OnInit {
  @Input() question:Question;
  session: Session;
  userService: UserService;
  qService: QuestionaryService;
  options: Option[] = null;
  newOption: Option = new Option();
  newOption2: Option = new Option();
  currentLogic: Logic = null;
  showLogicEditor = false;
  matchLogics: MatchLogic[];
  group: any;
  jsonLogic: JsonLogic = null;

  constructor (private _location:Location, private route: ActivatedRoute, private router: Router, userService: UserService, questionService:QuestionaryService) {
    this.userService = userService;
    this.session = userService.session;
    this.qService = questionService;
  }
  ngOnInit():void  {
  }

  ngOnChanges(changes: SimpleChange) :void {
    if (changes['question'] != undefined) {
      this.options = this.question.options;
      this.newOption.question_id = this.question.uid;
      if (this.question.type == "level") {
        this.newOption2.question_id = this.question.uid;
      }
    }
  }

  setLogic(logic: Logic): void {
    this.currentLogic = logic;
    this.showLogicEditor = true;
    this.qService.getLogicHierarcy(logic)
      .then(ml => { 
        this.jsonLogic = ml;
        console.log(ml);
      });
  }

  addOption(): void {
    console.log(this.newOption);
    this.qService.addOption(this.newOption)
      .then(o => {
        this.options.push(o);
        this.newOption = new Option();
        this.newOption.question_id = this.question.uid;
       });
  }
  addMinMaxRange(): void {
    this.qService.addOption(this.newOption)
      .then(o => { this.options.push(o); this.newOption = new Option();
        this.newOption.question_id = this.question.uid });
    this.qService.addOption(this.newOption2)
      .then(o => { this.options.push(o); this.newOption2 = new Option();
        this.newOption2.question_id = this.question.uid });
  }

  getMaxLevel(): number{
    return (this.options[0].value > this.options[1].value)?this.options[0].value : this.options[1].value;
  }
  getMinLevel(): number {
    return (this.options[0].value < this.options[1].value)?this.options[0].value : this.options[1].value;
  }


  create() {
  }
  cancel() {
  }
}




