import { Component, OnInit, Input } from '@angular/core';
import { Location } from '@angular/common';
import { CoolLocalStorage } from "angular2-cool-storage";
import { Router, ActivatedRoute, Params} from "@angular/router";
import { Session, User } from "./../user/user"
import { UserService } from "./../user/user.service";
import { QuestionaryService } from "./../questionary/questionary.service";
import { TreeNode } from "./types";

@Component({
  moduleId: module.id,
  selector: 'tree-view',
  templateUrl: './tree-view.component.html'
})
export class TreeViewComponent implements OnInit {
  session: Session;
  userService: UserService;
  qService: QuestionaryService;
  @Input() nodes: TreeNode[];
  currentNode:TreeNode;

  constructor (private _location: Location, private route: ActivatedRoute, private router: Router, userService: UserService, questionService:QuestionaryService) {
    this.userService = userService;
    this.session = userService.session;
    this.qService = questionService;
  }
  ngOnInit():void  {
  }
  check(n:TreeNode):void {
    n.checked = !n.checked;
    this.checkRecursive(n, n.checked);
    console.log("La puta madre del nodo");
  }
  checkRecursive(n: TreeNode, state:boolean):void {
    var _this = this;
    if (n.children != null) {
      n.children.forEach( c => {
        c.checked = state;
        _this.checkRecursive(c, state);
        console.log("Recursive");
      });
    }
  }
  setCurrentNode(n: TreeNode) {
    this.currentNode = n;
    console.log(n);
  }
  toggle(n:TreeNode):void {
    n.expanded = !n.expanded;
  }
  getChecked(n:TreeNode):boolean {
    return n.checked;
  }

  checkChildren(n:TreeNode): boolean {
    return (n.expanded && n.children != null);
  }
  appendMatch(n: TreeNode) {
  }
  appendMatchLogic(n: TreeNode) {
  }
}




