import { Component, OnInit } from '@angular/core';
import { CoolLocalStorage } from "angular2-cool-storage";
import { Router } from "@angular/router";
import { Session, User } from "./../user/user"
import { UserService } from "./../user/user.service";
import { QuestionaryService } from "./questionary.service";
import { Questionary, Option, Step } from "./types";

@Component({
  moduleId: module.id,
  selector: 'questionary-add',
  templateUrl: './questionary-add.component.html'
})
export class QuestionaryAddComponent implements OnInit {
  session: Session;
  userService: UserService;
  qService: QuestionaryService;
  questionary: Questionary = new Questionary();

  constructor (private router: Router, userService: UserService, questionService:QuestionaryService) {
    this.userService = userService;
    this.session = userService.session;
    this.qService = questionService;
  }
  ngOnInit():void  {
  }
  create() {
    this.qService.addQuestionary(this.questionary);
    this.router.navigate(['/dashboard']);
  }
  cancel() {
    this.router.navigate(['/dashboard']);
  }
}




